<template>
  <div class="box" >
        <div class="top" @click.stop>
          <div class="top-bar">
          <div class="title"><span>配送至</span>
            <img v-on:click="closeModal" :src="`${$cdnImageUrl}/common/control-close.png`" alt="" />
            </div>
          </div>
          <div class="addr-content">
          <div v-if="location" class="nowAddress location" @click="chooseCurrent">
            <img :src="`${$cdnImageUrl}/location/icon_addr_bule.png`" alt="" />
            <span>定位地址({{location.district}}/{{location.township}})</span>
          </div>
          <div v-else class="nowAddress location" @click="chooseCurrent">
            <img :src="`${$cdnImageUrl}/common/location-icon-blue.svg`" alt="" />
            <span>点击自动定位</span>
          </div>
          <div class="addr-list">
              <div class="info" v-for="(item, index) in addrList" :key="index" @click="selectDefaultAddr(index)">
                <img :src="`${$cdnImageUrl}/location/icon_addr.png`" alt="" />
                <span>{{item.rn}} {{item.ar}}</span>
              </div>
              <div class="bottom-margin"></div>
          </div>
          </div>
          <div class="addr-bottom">
            <div class="addr-bottom-btn" @click.stop="otherLocation">
              选择其他地址
            </div>
          </div>
      </div>
  </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
/**
 * 选择地址弹窗
 * 使用标签<change-geographic-location>
 * 关闭回调 onHide
 * 选择位置回调 handleLocation
 */
export default {
  props: ['onlyread', 'showAddr', 'addrList'], // onlyread 只读属性 不修改store不进行localStroage存储
  data() {
    return {
      showLoading: false,
      location: null, // 定位信息
      show: true,
      windowHeight: '', // 获取屏幕高度
    }
  },
  methods: {
    ...mapMutations(['setLocationInfo', 'setLoaction']),
    ...mapActions(['getLatLongByLocationInfo']),
    /**
     * 选择定位地址
     */
    chooseCurrent() {
      if (this.location) {
        if (!this.onlyread) {
          localStorage.setItem('latitude', this.location.latitude)
          localStorage.setItem('longitude', this.location.longitude)
          localStorage.setItem('provinceId', this.location.provinceId)
          localStorage.setItem('cityId', this.location.cityId)
          localStorage.setItem('districtId', this.location.districtId)
          localStorage.setItem('townshipId', this.location.townshipId)
          // localStorage.setItem('location', JSON.stringify(this.location));
          // localStorage.setItem('aId', '');
          this.setLocationInfo(this.location)
          this.setLoaction({
            latitude: this.location.latitude,
            longitude: this.location.longitude
          })
        }
        this.$emit('handleLocation', this.location)
        // 定位成功关闭弹窗
        this.closeModal()
      } else {
        this.getLocation()
      }
    },
    /**
     * @description 关闭弹窗
     */
    closeModal() {
      // this.$emit('handleLocation', this.location)
      this.$emit('onHide')
    },
    /**
     * @description 从store获取经纬度和地理位置
     */
    getLocation() {
      this.$store.dispatch('doLocation', true).then(res => {
        if (res === 400) {
          this.$toast('定位失败，请自行选择位置信息')
        } else {
          this.$toast('自动定位成功')
          this.$store.dispatch('doLocationInfo', { latitude: res.latitude, longitude: res.longitude, force: true }).then(result => {
            this.location = result
            this.location.aId = '';
            console.log('自动定位成功：', result)
            this.$emit('handleLocation', result)
            // 定位成功关闭弹窗
            this.closeModal()
          })
        }
      })
    },
    otherLocation() {
      this.$emit('otherLocation')
    },
    selectDefaultAddr(index) {
      let data = this.addrList[index]
      // data.de = 1
      let locationInfo = {
        region: data.rn,
        provinceId: data.pi,
        province: data.pt,
        cityId: data.ci,
        city: data.ct,
        districtId: data.ri,
        district: data.rt,
        townshipId: data.si,
        township: data.st,
        latitude: '',
        longitude: ''
      }
      this.getLatLongByLocationInfo(locationInfo).then(res => {
        console.log('getLatLongByLocationInfo:', res)
        if (res === 400) {
          this.$toast('根据位置信息获取经纬度失败')
        } else {
          locationInfo.latitude = res.latitude;
          locationInfo.longitude = res.longitude;
          locationInfo.aId = data.id;
          locationInfo.district = locationInfo.region.split(' ')[2]
          // if (!this.onlyread) {
          localStorage.setItem('latitude', res.latitude)
          localStorage.setItem('longitude', res.longitude)
          localStorage.setItem('provinceId', locationInfo.provinceId)
          localStorage.setItem('cityId', locationInfo.cityId)
          localStorage.setItem('districtId', locationInfo.districtId)
          localStorage.setItem('townshipId', locationInfo.townshipId)
          // localStorage.setItem('location', JSON.stringify(locationInfo));
          // localStorage.setItem('aId', data.id);
          this.setLocationInfo(locationInfo)
          this.setLoaction(res)
          // }
          this.closeModal()
          this.$emit('handleLocation', locationInfo)
        }
      })
    },
  },
  mounted() {
    this.showLoading = true
  },
  beforeDestroy() {
  }
}
</script>

