<template>
  <div class="mixin-container">
    <!-- 选择地理位置 -->
    <van-popup v-model="showChooseLoaction" position="bottom" :style="{height:'9.6rem', 'border-radius': '12px 12px 0 0'}"  @close="onHide" @click="onHide" get-container="#app-warp">
       <lazy-change-geographic-location v-if='showChooseLoaction' :isShowBack="true" :storeId="storeId" @onHide="onHide" @closeAndBack="closeAndBack" @handleLocation="handleGeoLocation" />
    </van-popup>
    <!-- 地址选择列表 -->
    <van-popup v-model="addrListShow" position="bottom" :style="{height:'9.6rem', 'border-radius': '12px 12px 0 0'}"  @close="onHideAddr" @click="onHideAddr" get-container="#app-warp">
       <addr-select :addrList="addrData" @onHide="onHideAddr" @handleLocation="handleLocation"  @otherLocation="otherLocationOption" ></addr-select>
    </van-popup>
  </div>
</template>

<script>
import addrSelect from './components/addr-select'

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
const lazyChangeGeographicLocation = () => import('@/components/addrChooseList/components/change-geographic-location.vue')

export default {
  props: ['storeId'],
  components: {
    // goodsInfoInfo,
    lazyChangeGeographicLocation,
    addrSelect,
  },
  data () {
    return {
      showChooseLoaction: false,
      addrListShow: false,
      addrData: [],
      addrLoad: false
    }
  },
  created () {
    if (this.$store.getters.getLoginStatus) {
      this.getAddress()
    }
  },
  computed: {
  },
  methods: {
    openAddr() {
      this.addrListShow = true
    },
    changeLocation (changeModal) {
      this.showChooseLoaction = changeModal
    },
    /**
     * @description 点击选择所在地区 取消关闭回调
     */
    onHide () {
      this.showChooseLoaction = false
    },
    closeAndBack() {
      this.showChooseLoaction = false
      this.addrListShow = true
    },
    handleGeoLocation(data) {
      this.showChooseLoaction = false
      this.handleLocation(data)
    },
    /**
     * @description 点击选择所在地区 确认关闭回调
     */
    handleLocation (data) {
      this.$emit('handleLocation', data)
    },
    /**
     * @description  我的收获地址列表
     * 返回接口请求数据
    */
    getAddress() {
      const context = this;
      // const data = {}
      personalApi.getAddressList(null, true).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.addrData = _resData.data || [];
        }
      })
    },

    onHideAddr() {
      // this.handleLocation()
      this.addrListShow = false;
    },
    otherLocationOption() {
      if (!this.addrLoad) {
        lazyChangeGeographicLocation().then(() => {
          this.addrLoad = true;
          this.addrListShow = false
          this.showChooseLoaction = true
        })
        return;
      }
      this.addrListShow = false
      this.showChooseLoaction = true
    },
  },
  watch: {
  },
}
</script>
<style scoped lang="scss">
.van-popup{
  right: 0;
  @include max-width;
}
.mixin-container ::v-deep .van-popup {
    border-radius: 0.24rem 0.24rem 0 0;

}
</style>
